import React from 'react';
import Typewriter from 'typewriter-effect';

// CSS
import * as S from './style';

interface HeroProps {
  name: string;
}

const Hero = ({ name }: HeroProps) => {
  return (
    <S.HeroWrapper>
      <Typewriter
        options={{
          autoStart: true,
          loop: true,
        }}
        onInit={typewriter => {
          typewriter
            .typeString('! <br />')
            .pauseFor(1000)
            .typeString('')
            .pauseFor(1000)
            .deleteChars(3)
            .typeString('<strong></strong>   <br />')
            .typeString(`${name} .`)
            .pauseFor(2500)
            .start();
        }}
      />
    </S.HeroWrapper>
  );
};

export default Hero;
